@import "src/css/colors.scss";

#contact {
  background-color: $backgroundColor;
  color: $primaryColor;

  font-size: 4vw;
  text-align: center;

  padding: 10%;
  padding-top: 17%;
  padding-bottom: 15%;
}

#contact p { margin: 1%; }

#contact .quote::before{ content: "'"; }
#contact .quote::after{ content: "'"; }

#contact .quote{
  color: $primaryColor;
  font-size: 7vw;
  font-style: italic;

  margin-left: 1%;
}

.arrow-up{
  font-size: 1.2em;
}

.arrow-up a {
  color: $primaryColor;
  text-decoration: none;
}

.arrow:hover { color: black; }
