p { margin: 0; padding: 0px; }//clean up the default formating

#skills{ padding: 2%; }

.category {
  padding-left: 10%;
  font-size: 2.5em;
}

.category .logo {
  display: inline-block;
  margin-right: 1.5%;
  float: left;
}

.sub { font-size: 1.7em; font-weight: bold; }
.sub-title { font-weight: normal; display: inline-block; }
.sub .title { color: #d86411; }
.sub .blue { color: #0f5ddb; }

.category .title { display: inline-block; padding-top: 2%; }
.category .logo img { width: 6vw; height: 6vw; }

.tag {
  background-color: white;
  color: black;
  border: 2px solid black;

  display: inline-block;
  margin: 1%;
  padding: 0.5%;
}
