@import "src/css/colors.scss";

#nav-bar {
  padding: 1%;
}

#nav-bar div{
  display: inline-block;
}

#nav-bar .quote::before{ content: "'"; }
#nav-bar .quote::after{ content: "'"; }

#nav-bar .quote{
  color: $primaryColor;
  font-size: 1.95em;
  font-style: italic;

  margin-left: 1%;
}

#nav-bar .logo{
    width: 48px;
    height: 48px;

    float: left;
}

#nav-bar img:hover {
  cursor: pointer;
}
