body {
  margin: 0;
  padding: 0;
  font-family: Helvetica, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


#name {
  color: white;
  text-align: center;
  font-size: 9vw; }

#name .title {
  font-size: 5vw;
  margin: 0; }

#name .rocket {
  width: 7vw;
  height: 7vw; }

#name .left {
  display: inline-block; }

#name .right {
  display: inline-block; }

#nav-bar {
  padding: 1%; }

#nav-bar div {
  display: inline-block; }

#nav-bar .quote::before {
  content: "'"; }

#nav-bar .quote::after {
  content: "'"; }

#nav-bar .quote {
  color: white;
  font-size: 1.95em;
  font-style: italic;
  margin-left: 1%; }

#nav-bar .logo {
  width: 48px;
  height: 48px;
  float: left; }

#nav-bar img:hover {
  cursor: pointer; }

#intro {
  background-color: #f4ad42;
  padding-bottom: 3%; }

#intro #name {
  padding-top: 14%; }

#nav-bar img {
  width: 96px;
  height: 96px; }

.arrow {
  font-size: 0.5em; }

.arrow a {
  color: white;
  text-decoration: none;
  display: block;
  padding-top: 2%; }

.arrow:hover {
  color: black; }

p {
  margin: 0;
  padding: 0px; }

#skills {
  padding: 2%; }

.category {
  padding-left: 10%;
  font-size: 2.5em; }

.category .logo {
  display: inline-block;
  margin-right: 1.5%;
  float: left; }

.sub {
  font-size: 1.7em;
  font-weight: bold; }

.sub-title {
  font-weight: normal;
  display: inline-block; }

.sub .title {
  color: #d86411; }

.sub .blue {
  color: #0f5ddb; }

.category .title {
  display: inline-block;
  padding-top: 2%; }

.category .logo img {
  width: 6vw;
  height: 6vw; }

.tag {
  background-color: white;
  color: black;
  border: 2px solid black;
  display: inline-block;
  margin: 1%;
  padding: 0.5%; }

#contact {
  background-color: #f4ad42;
  color: white;
  font-size: 4vw;
  text-align: center;
  padding: 10%;
  padding-top: 17%;
  padding-bottom: 15%; }

#contact p {
  margin: 1%; }

#contact .quote::before {
  content: "'"; }

#contact .quote::after {
  content: "'"; }

#contact .quote {
  color: white;
  font-size: 7vw;
  font-style: italic;
  margin-left: 1%; }

.arrow-up {
  font-size: 1.2em; }

.arrow-up a {
  color: white;
  text-decoration: none; }

.arrow:hover {
  color: black; }

